	var that;
export default {
	data() {
		return {
			TabCur:0,
			scrollLeft: 0,
			navtitle: [{ id: 0, name: '平台商品' }, { id: 1, name: '商家商品' }, { id: 2, name: '我的商品' }],
			list: [],
			page: 1,
			nomore: false,
			nolist: false
		};
	},
	methods: {
		tabSelect(e) { 
			this.TabCur = e.currentTarget.dataset.id;
			(this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60);
			that.page = 1;
			that.nomore = false;
			that.nolist = false;
			that.list = [];
			that.getlist();
		},
		getlist(){ 
			that.$request
				.request({
					url: that.api.anchor_order.goods_list,
					method: 'GET',
					data:{
						type: Number(that.TabCur) + 1,
						page: that.page
					}
				})
				.then(res=>{
					console.log(res)
					if (res.length > 0) {
						var list = res;
						that.list = that.list.concat(list);
						that.page = that.page + 1;
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了!');
								that.nomore = true;
							}
						} else {
							that.nolist = true;
						}
					}
					uni.stopPullDownRefresh()
				})
		},
		clickicon(e){ 
			if(that.TabCur == 0){
				var url = '/pages/anchor/goods/storeroom/addGoodstostor/addGoodstostor'
			}else if(that.TabCur == 1){
				var url = '/pages/anchor/goods/storeroom/addGoodslive/addGoodslive'
			}else if(that.TabCur == 2){
				var url = '/pages/anchor/goods/storeroom/addGoods/addGoods'
			}
			uni.navigateTo({
				url: url
			})
		},
		todetail(e){ 
			var id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url: '/pages/liveBroadcast/goods/goodsInfo/goodsInfo?id=' + id
			})
		},
		del(e){ 
			var id = e.currentTarget.dataset.id;
			let index=e.currentTarget.dataset.index;
			uni.showModal({
				title:"提示",
				content:"确定要删除该产品吗?",
				success(res){
					if(res.confirm){
						if(that.TabCur ==0 || that.TabCur == 1){
							that.$request
								.request({
									url: that.api.anchor_order.del_relation_goods,
									method: 'GET',
									data:{
										goods_id: id
									}
								})
								.then(res=>{
									console.log(res)
									that.list.split(index,1)
								})
						}else if(that.TabCur == 2){
							that.$request
								.request({
									url: that.api.anchor_order.del_anchor_goods,
									method: 'GET',
									data:{
										goods_id: id
									}
								})
								.then(res=>{
									console.log(res)
									that.list.split(index,1)
								}) 
						}
					}
				}
			}) 
		}
	},
	onLoad() {
		 that = this;
		that.getlist();
	},
	onReachBottom() {
		that.getlist();
	},
	onPullDownRefresh() {
		that.page=1;
		that.list=[];
		that.getlist();
	}
};
